import React from 'react';
import { withRouter } from 'react-router-dom';
import { Button, Modal, Alert, Image } from 'antd';
import { getEvent } from '../../../services/eventService';
import anhminhhoa from '../../../assets/images/anhminhhoa.jpg';
import bg from '../../../assets/images/bg.png';
class home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            alert: 'Li Server 3',
            status_Event: 3,
            dataEvent: {},
            isOpenDialog: false,
        }
    }

    async componentDidMount() {
        await this.getEvent();
    }
    checkEvent = (dataEvent) => {
        if (dataEvent.is_activate == true) {
            let DateNow = new Date();
            let expiry_date = new Date(dataEvent.expiry_date);
            if (DateNow > expiry_date) {
                this.setState({
                    alert: ' ht hn ng k',
                    status_Event: 4,
                })
            }
            if (DateNow < expiry_date) {
                this.setState({
                    alert: 'S kin ang m',
                    status_Event: 0,
                })
            }
        } else {
            this.setState({
                alert: 'S kin ang ng',
                status_Event: 4,
            })
        }
    }
    getEvent = async () => {
        try {
            let data = await getEvent();
            if (data && data.data && data.data.success == 1) {
                this.checkEvent(data.data.data);
            } else {
                this.setState({ status_Event: 2, alert: 'Li Server 2' })
            }
        } catch (e) {
            this.setState({ status_Event: 1, alert: 'Li Server 1' })
            console.log('Li Server 1', e);
        }
    }
    openDialog = (input) => {
        this.setState({ isOpenDialog: input })
    }
    onclickPage = () => {
        if (this.state.status_Event == 0) {
            this.props.history.push(`/home/form`);
        }
    }
    render() {
        let status_Event = this.state.status_Event;
        return (

            <div className='h-screen w-screen bg-center sm:bg-cover bg-no-repeat 
            flex items-center justify-center' style={{ backgroundImage: `url(${bg})` }}>
                <div className='space-y-[5px]'>
                    <div className='s'>
                        <img src={require(`../../../assets/images/LOGO_CSV23.png`).default} className='h-[100px] sm:h-[200px] md:h-[250px] w-auto' />
                    </div>
                    <div className='flex items-center justify-center'>
                        <div className='space-y-[5px]  p-[10px] shadow-md rounded-[5px]'>
                            {/* {status_Event == 0 ?
                                <div className='text-center text-[14px]'><span className='text-white'>{this.state.alert}</span></div>
                                //<Alert message={this.state.alert} type="success" showIcon />
                                :
                                <div className='text-center text-[14px]'><span className='text-red-500'>{this.state.alert}</span></div>
                                //<Alert message={this.state.alert} type="error" showIcon />
                            } */}
                            {status_Event == 0 ?
                                <Button onClick={() => this.openDialog(true)}
                                    className='shadow-md bg-gradient-to-t from-[#673de4] via-[#b577c2] to-[#673de4]
                                    font-[500] text-white hover:scale-105'
                                    size="large" type="default" shape="round" >
                                    NG K MUA V
                                </Button>
                                :
                                <Button
                                    className='shadow-md bg-gradient-to-t from-[#673de4] via-[#b577c2] to-[#673de4]
                                    font-[500] text-white hover:scale-105'
                                    size="large" type="default" disabled >
                                    NG K MUA V
                                </Button>
                            }

                        </div>
                    </div>
                </div>
                <Modal title="Quy nh trc khi mua v" open={this.state.isOpenDialog}
                    okText={'Tip tc'} okType={'default'} cancelText={'Hy b'}
                    onOk={() => this.onclickPage()}
                    onCancel={() => this.openDialog(false)}
                >
                    <div className=' p-[10px] border shadow-md rounded-[5px] space-y-[5px] italic text-red-700'>
                        <div className='space-y-[5px]'>
                            <div>
                                <label>1. Khng thay i thng tin ghi ch t ng v s tin chuyn khon sau khi qut m QR.</label>
                            </div>
                            <div className='flex items-center justify-center'> <Image src={anhminhhoa} width={200} /></div>
                        </div>
                        <div>
                            <label>2. Khng thot chng trnh trong qu trnh i xc thc thanh ton.</label>
                        </div>
                        <div>
                            <label>3. in thng tin MSSV chnh xc v v s c gi qua email sinh vin. Kim tra mc spam sau khi thanh ton thnh cng nu khng thy mail trong hp th.</label><br />
                        </div>
                        <div>
                            <label>4. Bm vo "Tip tc", tc l bn "ng " vi nhng quy nh trn.</label>
                        </div>
                    </div>
                </Modal>
            </div >
        )
    }
}

export default withRouter(home);